import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getDormRoomStudents = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: '/DormRoom_Student',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getDormRoomStudentById = id =>
  request({
    prefix,
    url: `/DormRoom_Student/${id}`,
    method: 'GET',
  });

export const createDormRoomStudents = ({ studentId, startDate, dormRoomId, note }) =>
  request({
    prefix,
    url: `/DormRoom_Student`,
    params: {},
    data: { studentId, startDate, dormRoomId, note },
    method: 'POST',
  });

export const createOutRoomStudents = ({
  studentId,
  startDate,
  address,
  phoneNo,
  houseLord,
  note,
}) =>
  request({
    prefix,
    url: `/DormRoom_Student/CreateOutRoom_Student`,
    params: {},
    data: { studentId, startDate, address, phoneNo, houseLord, note },
    method: 'POST',
  });

export const deleteDormRoomStudents = id =>
  request({
    prefix,
    url: `/DormRoom_Student/${id}`,
    method: 'DELETE',
  });

export const deleteOutRoomStudents = id =>
  request({
    prefix,
    url: `/DormRoom_Student/DeleteOutRoom_Student/${id}`,
    method: 'DELETE',
  });

export const exportDormRoomStudents = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
}) =>
  requestDownload({
    defaultFileName: 'Exported File.xlsx',
    prefix,
    url: `/DormRoom_Student/ExportDormRoomStudents`,
    method: 'POST',
    data: { trainTypeId, facultyId, courseId, studyFieldId, classId },
  });



